.active{color:#EA591A}*{margin:0;padding:0;box-sizing:border-box;scroll-behavior:smooth}#particles-js{height:100vh;width:100%;position:fixed;z-index:-1}body{transition:0.7s all;background-color:#fff;overflow-x:hidden;overscroll-behavior-y:contain;-webkit-user-select:none;-webkit-tap-highlight-color:transparent;-webkit-touch-callout:none}.dark{background-color:#000000 !important}:root{--primary-color: #d5c455;--primary-color2: #fddb3a;--title-color: #141313;--text-color: #777777;--border-color: #201f1f17;--gradient-color: linear-gradient(90deg, #d5c455 0%, #fddb3a 100%);--shadow-color: 0px 10px 30px rgba(118,85,225,0.3);--box-shadow: 0px 10px 30px rgba(57,56,61,0.205);--roboto: 'Roboto', cursive;--color-dark: rgb(17, 17, 17);--color-gris: rgb(75, 75, 75);--sombra-dark: 10px 10px 20px rgba(170,170,170, .16);--sombra-light: 10px 10px 20px rgba(0,0,0,.16)}.ctn_pre_header{height:82px}.header_area .main-menu .navbar .navbar-brand{padding:0 2rem 0 5rem}.header_area .main-menu .navbar .navbar-brand img{top:0px;position:absolute;width:160px;height:80px}.header_area .main-menu .navbar{padding:26px 8rem}.header_area .main-menu .nav-item .nav-link{font:normal 600 15px/2px var(--roboto);font-family:var(--roboto);text-transform:uppercase;color:var(--title-color)}.header_area .main-menu .navbar-nav .active a{background:var(--gradient-color);-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-clip:text}.header_area .main-menu .navbar-nav a:hover{background:var(--gradient-color);-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-clip:text}.site-main{position:relative}.site-main svg{width:100%;height:100vh;position:absolute;left:0;top:-82px;z-index:-1}.site-main svg path{-webkit-animation:key_one 5s ease -1.25s infinite;animation:key_one 5s ease -1.25s infinite;fill:url(#shape-gradient)}@-webkit-keyframes key_one{0%,100%{transform:translate3d(0, -10px, 0)}50%{transform:translate3d(0, 0px, 0)}}@keyframes key_one{0%,100%{transform:translate3d(0, -10px, 0)}50%{transform:translate3d(0, 0px, 0)}}.site-main .side-banner .site-title h1{margin-top:80px;font-size:3.5rem}.site-main .side-banner .site-title h4{font-size:1.5rem}.site-main .side-banner .banner-image+img{padding:7rem 0;width:90rem}.title-text{font:normal bold 50px/72px var(--roboto)}.site-buttons .d-flex{width:100%;justify-content:space-between}.site-buttons .d-flex a{text-align:center;width:47%}.button{padding:.8rem 2.2rem;font:normal 600 16px/20px var(--roboto);position:relative;border:3px solid transparent;border-radius:4px}.button.primary-button{background:var(--gradient-color);background-clip:padding-box;color:whitesmoke;transition:all 0.8s;box-shadow:var(--box-shadow)}.button.primary-button:hover{background:whitesmoke;background-clip:padding-box;color:black}.button.primary-button::after,.button.secondary-button::after{position:absolute;top:-2px;left:-2px;bottom:-2px;right:-2px;background:var(--gradient-color);content:'';z-index:-1;border-radius:4px}.button.secondary-button{background:white;background-clip:padding-box;transition:all 0.8s}.button.secondary-button:hover{background:var(--gradient-color);color:whitesmoke;box-shadow:var(--box-shadow)}.pdr{padding-right:5px}
