.word{position:absolute;opacity:0}.letter{display:inline-block;position:relative;transform:translateZ(25px);transform-origin:50% 50% 25px}.letter.owt{transform:rotateX(90deg);transition:transform 0.32s cubic-bezier(0.55, 0.055, 0.675, 0.19)}.letter.behind{transform:rotateX(-90deg)}.letter.in{transform:rotateX(0deg);transition:transform 0.38s cubic-bezier(0.175, 0.885, 0.32, 1.275)}.palabraUno{text-transform:uppercase;color:#FFCE41}.palabraDos{text-transform:uppercase;color:#F3AA3B}.palabraTres{text-transform:uppercase;color:#f89f19}
